import React, { Component, useState } from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';

const CREATE_LOCKBOX_MUTATION = gql`
  mutation CREATE_LOCKBOX_MUTATION(
    $amount: Float!
    $branch: String!
    $box: String!
    $time: String!
    $date: String!
  ) {
    createLockbox(
      data: {
        amount: $amount
        branch: $branch
        box: $box
        time: $time
        date: $date
      }
    ) {
      amount
      branch
      box
    }
  }
`;

function BoxSubmit() {
  // state = {
  //   branch: '',
  //   box: '',
  //   time: '',
  //   data: '',
  //   amount: 0
  // };

  const [boxForm, setBoxForm] = useState({
    branch: '',
    box: '',
    time: '',
    date: '',
    amount: 0
  });
  // const saveToState = (e) => {
  //   if (e.target.name === 'amount') {
  //     this.setState({ [e.target.name]: parseFloat(e.target.value) });
  //   } else this.setState({ [e.target.name]: e.target.value });
  // };
  const printValues = (e) => {
    e.preventDefault();
    console.log(boxForm.branch, boxForm.box, boxForm.date, boxForm.amount);
  };
  // const handleFormReset = () => {
  //   this.setState(() => this.state);
  // };
  const updateField = (e) => {
    if (e.target.name === 'amount') {
      setBoxForm({
        ...boxForm,
        [e.target.name]: parseFloat(e.target.value)
      });
    } else {
      setBoxForm({
        ...boxForm,
        [e.target.name]: e.target.value
      });
    }
  };
  return (
    <Mutation mutation={CREATE_LOCKBOX_MUTATION} variables={boxForm}>
      {(createLockbox, { error, loading }) => (
        <div>
          <form
            method="post"
            onSubmit={async (e) => {
              e.preventDefault();
              console.log(boxForm);
              await createLockbox();
              setBoxForm({
                branch: '',
                box: '',
                time: '',
                date: '',
                amount: 0
              });
            }}
            // onSubmit={printValues}
          >
            <label htmlFor="branch">
              Branch
              <input
                type="text"
                name="branch"
                placeholder="Branch"
                onChange={updateField}
              />
            </label>
            <label htmlFor="box">
              Box
              <input
                type="text"
                name="box"
                placeholder="Box"
                onChange={updateField}
              />
            </label>
            <label htmlFor="time">
              Time
              <input
                type="text"
                name="time"
                placeholder="Time"
                onChange={updateField}
              />
            </label>
            <label htmlFor="amount">
              Amount
              <input
                type="number"
                step={0.1}
                name="amount"
                placeholder="Amount"
                onChange={updateField}
              />
            </label>
            <label htmlFor="date">
              Date
              <input
                type="text"
                name="date"
                placeholder="Date"
                onChange={updateField}
              />
            </label>
            <button type="submit">Submit</button>
          </form>
        </div>
      )}
    </Mutation>
  );
}

export default BoxSubmit;
